/* CoinApp.css */
.coinApp-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 95%;
  padding: 20px;
  background-color: #ffffff;
}

.coinApp-title {
  width: 80%;
  padding: 10px;
  background-color: #e3f2fd;
  border: 1px solid #1e88e5;
  border-radius: 10px;
  margin-bottom: 20px;
  text-align: center;
  font-size: 1.5em;
  color: #1e88e5;
}

.coinApp-instructions {
  width: 80%;
  padding: 20px;
  background-color: #fff;
  border: 2px solid #1e88e5;
  border-radius: 10px;
  margin-bottom: 20px;
  text-align: center;
  color: #333;
}

.problemContainer {
  width: 97%;
  padding: 15px;
  background-color: #fff;
  border: 2px solid #1e88e5;
  border-radius: 10px;
  margin-bottom: 20px;
  color: #333;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.problemBlock {
  display: flex;
  align-items: center;
  width: 103%;
  margin-bottom: 10px;
}

.pic {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}

.userInput {
  flex: 1;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.buttonContainer {
  display: flex;
  justify-content: center;
  width: 100%;
}

.submitBtn {
  width: 50%;
  padding: 15px;
  margin-top: 20px;
  border: none;
  border-radius: 5px;
  background-color: #6a9e34;
  color: #fff;
  font-size: 1.2em;
  cursor: pointer;
}

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.feedback.correct {
  color: green;
}

.feedback.incorrect {
  color: red;
}

.nextQuestionBtn,
.closeBtn {
  padding: 10px 20px;
  margin-top: 20px;
  border: none;
  border-radius: 5px;
  background-color: #1e88e5;
  color: #fff;
  cursor: pointer;
  margin-right: 10px;
}

.timer {
  position: fixed;
  top: 10px;
  right: 10px;
  font-size: 0.7em;
  color: #333;
}