.progress-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 95%;
  padding: 20px;
  background-color: #ffffff;
}

.progress-title {
  width: 80%;
  padding: 10px;
  background-color: #e3f2fd;
  border: 1px solid #1e88e5;
  border-radius: 10px;
  margin-bottom: 20px;
  text-align: center;
  font-size: 1.5em;
  color: #1e88e5;
}

.progress-content {
  width: 80%;
  padding: 20px;
  background-color: #fff;
  border: 2px solid #1e88e5;
  border-radius: 10px;
  margin-bottom: 20px;
  text-align: center;
  color: #333;
}

.result {
  margin: 10px 0;
  padding: 10px;
  border-radius: 5px;
}

.result.correct {
  background-color: #6a9e34;
  color: #fff;
}

.result.incorrect {
  background-color: #d32f2f;
  color: #fff;
}
