.home-container {
  text-align: center;
  padding: 20px;
}

.header {
  background-color: #f0f8ff;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
}

.features {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.feature {
  background-color: #e3f2fd;
  border: 1px solid #1e88e5;
  border-radius: 10px;
  padding: 20px;
  width: 30%;
  margin: 10px;
}

.cta-button {
  display: inline-block;
  padding: 10px 20px;
  margin-top: 10px;
  border-radius: 5px;
  background-color: #6a9e34;
  color: #fff;
  text-decoration: none;
}

.cta-buttons .cta-button {
  margin: 5px;
}