.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 95%;
  padding: 20px;
  background-color: #ffffff;
}

.login-title {
  width: 80%;
  padding: 10px;
  background-color: #e3f2fd;
  border: 1px solid #1e88e5;
  border-radius: 10px;
  margin-bottom: 20px;
  text-align: center;
  font-size: 1.5em;
  color: #1e88e5;
}

.login-content {
  width: 80%;
  padding: 20px;
  background-color: #fff;
  border: 2px solid #1e88e5;
  border-radius: 10px;
  margin-bottom: 20px;
  text-align: center;
  color: #333;
}

.login-box {
  width: 80%;
  padding: 15px;
  background-color: #fff;
  border: 2px solid #1e88e5;
  border-radius: 10px;
  margin-bottom: 10px;
  color: #333;
}

.input-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.input-field {
  width: 70%;
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  border: 1px solid #1e88e5;
  border-radius: 50px;
}

.login-button {
  width: 50%;
  padding: 15px;
  margin-top: 20px;
  border: none;
  border-radius: 5px;
  background-color: #6a9e34;
  color: #fff;
  font-size: 1.2em;
  cursor: pointer;
}

.error-message {
  color: red;
  margin-top: 10px;
}

.success-message {
  color: green;
  margin-top: 10px;
}
